@import 'reset';
@import 'global';

/* Contents Header */
header {
    background-color: $light-blue !important;
    color: $white;
    text-align: center;
    border-radius: 0 !important;
    
    h1 {
        color: $white;
    }
}
/*-- Contents Header --*/

/* Contents Navbar */
nav.navbar {
    background-color: $blue !important;
    font-size: 16px;
    color: $white;

    .navbar-toggler {
        background-color: $green;
    }
    .navbar-collapse {
        justify-content: space-between;
        .current, .current:hover, a:hover {
            background-color: $green;
            color: $white !important;
        }
    }
    form {
        .btn {
            background-color: $green;
            color: $white;
        }
    }
}
/*-- Contents Navbar --*/

/* Contents Article */
.container-fluid article {
    position: relative;
    background-color: $light-white;
}

/* Social-Media Button */
.social-btn {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 0;
    z-index: 10;
    a {
        flex-grow: 1;
        text-align: center;
        padding: 10px;
        color: $white !important;
        font-size: 20px;
    }
    a.fb {
        background-color: #3B5998;
    }
    a.ig {
        background-color: #DD2A7B;
    }
    a.github {
        background-color: #171515;
    }
    a.linkedin {
        background-color: #007BB5;
    }
    a.email {
        background-color: #EA4335;
    }
}
/*-- Social-Media Button --*/

/* Datetime dan Caption */
#date-time {
    p {
        padding: 5px 10px;
        background-color: $green;
        color: $white;
        font-size: 13px;
        font-weight: bold;
        text-align: center;
        line-height: 20px;
    }
}
/*-- Datetime dan Caption --*/

/* Blockquote Style */
.card-body {
    blockquote {
        margin: 20px 0;
        padding: 10px 20px;
        font-style: oblique;
        border-radius: 10px;
        background-color: $light-white;
        position: relative;
        border-left: 7px solid $green;
    }
    blockquote::before, blockquote::after {
        position: absolute;
        font-size: 120px;
        color: rgba(0, 183, 98, 0.251);
        font-family: serif;
    }
    blockquote::before {
        content: open-quote;
        top: 40px;
        left: -10px;
    }
    blockquote::after {
        content: close-quote;
        bottom: -15px;
        right: 20px;
    }
}
/*-- Blockquote Style --*/

/* Breadcrumb */
.breadcrumb {
    font-size: 13px;
    background-color: $light-white;
    a {
        color: $white !important;
        background-color: $blue;
        border-radius: 10px;
        padding: 2px 10px;
        margin: 0 5px;
    }
}
/*-- Breadcrumb --*/

#post {    
    .post-content {
        display: flex !important;

        /* Content Main */
        .card {
            width: 70%;

            img {
                width: 100%;
                border-radius: 5px;
            }
            h2, h3 {
                line-height: 25px;
                margin: 10px 0;
                font-weight: bolder;
            }
            h2 {
                color: $black;
                background-color: $light-white;
                padding: 10px 15px;
                border: 4px dotted $black;
                line-height: 40px;
                margin-bottom: 15px;
            }
            h3 {
                display: inline-block;
                margin-top: 30px;
            }
            
        }
        /*-- Content Main --*/

        /* Content Sidebar */
        aside {
            width: 30%;
            background-color: $white;
            padding: 0 20px 30px 20px;
            
            h4 {
                color: $blue;
                background-color: $light-white;
                padding: 5px 10px;
                border-radius: 7px;
                margin: 30px 10px 20px 0;
                border: 3px dotted $blue;
                font-weight: bold;
                text-align: center;
            }
            
            .sidebar-image {
                margin-right: 10px;
                border-radius: 5px;
                box-shadow: 0 0 15px $light-white;
                padding: 25px 20px;
                text-align: center;
                
                img {
                    width: 50% !important;
                    border-radius: 50% !important;
                }
                .btn {
                    border: 1px solid $green;
                    border-radius: 20px;
                    font-size: 15px;
                    color: $green !important;
                }
                .btn:hover {
                    border: 1px solid $blue;
                    color: $blue !important;
                }
            }
        }
        /*-- Content Sidebar --*/
    }
}

/* Postingan */
.card-body {
    /* Post Label/Tag */
    .badge {
        background-color: $green;
        color: $white !important;
        border-radius: 15px;
        padding: 5px 10px;
        font-weight: normal;
    }
    .badge:hover {
        text-decoration: underline;
    }
    /*-- Post Label/Tag --*/

    p, li {
        text-align: justify;
    }
    ol {
        counter-reset: list;
    }
    li {
        counter-increment: list;
    }
    li::before {
        content: counter(list);
        background-color: $blue;
        padding: 0 5px;
        border-radius: 5px;
        color: $white;
        font-weight: bold;
        margin-right: 5px;
    }      
    p.featured-link {
        margin: 10px 0;
        padding: 10px;
        border-left: 7px solid $black;
        background-color: $light-white;
        a {
            background-color: $green;
            color: $white !important;
            margin-left: 5px;
            padding: 2px 5px;
            border-radius: 5px;
        }
    }
}

/* Nama Penulis dan Tanggal Posting */
.post-attribute {
    margin-bottom: 10px;
    font-size: 13px;
    align-items: center;
    color: $light-gray;
}
.date-post {
    margin-left: 10px;
}
#lists {
    .card-img-top, .card-body { //blog content lists
        width: 50%;
    }
    .post-attribute {
        img {
            width: 30px;
            border-radius: 50%;
            margin-right: 10px;
        }
    }
}
/*-- Nama Penulis dan Tanggal Posting --*/

/* Share on Social Media */
.share {
    p {
        a {
            line-height: 10px;
            margin: 0 3px;
            padding: 10px;
            width: 35px;
            border-radius: 50%;
            display: inline-block;
            color: $white !important;
        }
        a.fb {
            background-color: #3B5998;
        }
        a.twitter {
            background-color: #00ACEE;
        }
        a.linkedin {
            background-color: #007BB5;
        }
        a.wa {
            background-color: #25D366;
        }
        a.telegram {
            background-color: #0088CC;
        }
    }
}
/*-- Share on Social Media --*/

/* Post Tags */
.tag {
    margin: 20px;
    padding: 20px;
    background-color: $white;
    border-radius: 5px;
    line-height: 30px;
    h2 {
        background-color: $blue;
        border-radius: 5px;
        text-align: center;
        color: $white;
        padding: 10px;
        margin-bottom: 20px;
    }
    li {
        counter-increment: list;
    }
    li::before {
        content: counter(list);
        background-color: $blue;
        padding: 0 5px;
        border-radius: 5px;
        color: $white;
        font-weight: bold;
        margin-right: 5px;
    }      
}
/*-- Post Tags --*/

/* Books Style */
#book {
    .book-desc {
        background-color: $white;
        a {
            background-color: $blue;
            color: $white !important;
            font-weight: bold;
            font-size: 13px;
        }
    }
}
/*-- Books Style --*/

/* Search Style */
.search {
    section {
        margin: 20px 0;
        padding: 20px;
        background-color: $white;
        h3 {
            margin-bottom: 20px;
            span {
                display: inline-block;
                margin-right: 10px;
                padding: 5px 10px;
                background-color: $green;
                color: $white;
                border-radius: 5px;
            }
        }
        .search-content {
            display: flex;
            img {
                width: 30%;
                margin-right: 20px;
            }
            p {
                width: 70%;
                a {
                    display: inline-block;
                    background-color: $green;
                    padding: 3px 10px;
                    border-radius: 5px;
                    color: $white !important;
                }
            }
        }
    }
}
/*-- Search Style --*/

/* Pages Style */
// author, contact, about, dsb
#page {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .page-desc {
        width: 30%;
        > img {
            width: 50%;
            border-radius: 50%;
        }    
    }
    .page-about {
        width: 70%;
        margin-left: 30px;
        text-align: left;
    }
    p {
        margin-bottom: 10px;
    }
}
/*-- Pages Style --*/

/*-- Postingan --*/

/* Pagination */
.page-link {
    background-color: $green;
    margin: 0 2px;
    padding: 10px 15px;
    color: $white !important;
    font-size: 15px;
}
.page-link:hover, .current-link {
    background-color: $blue;
}
/*-- Pagination --*/

/*-- Article Contents --*/

/* Footer Contents */
footer {
    font-size: 13px;
    font-weight: bold;
    background-color: $white;
    text-align: center;

    /* Footer Attention */
    .attention {
        color: $white;
        background-color: $green;
        padding: 10px 20px 0 20px;
    }
    /*-- Footer Attention --*/

    li {
        display: inline-flex;
        a {
            color: $black;
        }
    }
    li, .credit {
        a {
            padding: 20px;
        }
    }
    .credit {
        padding: 20px;
        background-color: $blue;
        color: $white;
    }
}
/*-- Footer Contents --*/

@media screen and (max-width: 990px) {
    /* Mobile Navigation */
    nav {
        z-index: 10;
        position: sticky;
        top: 0;
    }
    /*-- Mobile Navigation --*/

    /* Social-Media Button */
    .social-btn {
        flex-direction: row;
        bottom: 0;
        width: 100%;
    }
    /*-- Social-Media Button --*/

    #lists {
        .card-img-top, .card-body { // blog content lists
            width: 100%;
        }
    }

    #post {
        .post-content {
            flex-direction: column;
            
            .card, aside { // blog, book content posts
                width: 100%;
            }
            aside {
                h4, .sidebar-image {
                    margin-right: 0;
                }
                .sidebar-image {
                    display: flex;
                    align-items: center;
                    
                    .image {
                        width: 25%;

                        img {
                            width: 100% !important;
                        }
                    }
                    .image-desc {
                        width: 75%;
                        text-align: left;
                        padding-left: 20px;
                    }
                }
            }
        }
    }

    /* Article Contents */
    article {
        position: relative;
    }

    .search-content {
        img, p {
            width: 50% !important;
        }
    }
    /*-- Article Contents --*/

    #page {
        flex-direction: column;
        .page-desc, .page-about {
            width: 100%;
        }
        .page-about {
            text-align: center;
            margin-top: 20px;
            margin-left: 0;
        }
    }
}

@media screen and (max-width: 576px) {

    .card-body {
        text-align: center;
    }

    .search {
        section {
            flex-direction: column;
        }
        .search-content {
            flex-direction: column;
            img, p {
                width: 100% !important;
            }
            p {
                margin-top: 15px;
            }
        }
    }

    aside {
        .sidebar-image {
            flex-direction: column;
            
            .image {
                width: 40% !important;
            }
            .image-desc {
                width: 100%;
                text-align: center !important;
                padding-left: 0 !important;
            }

        }
    }

    footer {
        li {
            a {
                padding: 20px 10px;
            }
        }
        li, .credit {
            font-size: 13px;
        }
    }
}