@font-face {
    font-family: 'Montserrat';
    src: url('../fonts/Montserrat-Regular.ttf');
}

$font: Montserrat;
$black: #013557;
$white: #fff;
$green: #00B762;
$blue: #113A93;
$light-blue: #1148F2;
$light-gray: #777;
$light-white: #f3f3f3;

::selection {
    background-color: $black;
    color: $green;
}
body {
    font-family: $font;
    background-color: $light-white;
    color: $black;
}
.container-fluid {
    article {
        font-size: 15px;
        line-height: 30px;
    }
}
hr {
    border: 1px solid $light-white;
    margin: 10px 0;
}
h1, h2, h3, h4 {
    font-weight: bold;
}
h1 {
    font-size: 35px !important;
}
h2 {
    font-size: 28px !important;
}
h3 {
    font-size: 20px !important;
    background-color: $light-white;
    border: 4px dotted $blue;
    padding: 10px 15px;
    border-radius: 10px;
    color: $blue;
}
h4 {
    font-size: 18px !important;
}
.card {
    border: none;
    border-radius: 0;
}
table {
    width: 100%;
    border-bottom: 2px solid $light-white;
    th, td {
        padding: 5px 10px;
    }
    th {
        background-color: $green;
        color: $white;
    }
    tr:nth-of-type(even) {
        background-color: $light-white;
    }
}

/* Syntax Highlighting */
.language-plaintext {
    color: $white;
    font-size: inherit;
    background-color: $green;
    border-radius: 5px;
    padding: 2px 3px;
}
.highlight {
    padding: 7px 7px 1px 7px;
    font-size: inherit;
    line-height: 20px;
    border-radius: 5px;
    background-color: $light-white;
    max-height: 400px;
    text-align: left;
    .lineno {
        font-size: inherit;
        color: $black;
        padding: 0 5px;
    }
    .code {
        font-size: inherit;
    }
}
/*-- Syntax Highlighting --*/

/* Smooth Scrolling */
html {
  scroll-behavior: smooth;
}
/* -- Smooth Scrolling -- */

/* Scroll to Top */
#toTop {
    padding: 10px;
    background-color: $light-blue;
    color: $white;
    bottom: 20px;
    right: 10px;
    z-index: 10;
    display: none;
    cursor: pointer;
    position: fixed;
    opacity: .75;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=95)";
    border-radius: 50%;
    width: 45px;
    height: 45px;
    text-align: center;
}
    
#toTop:hover {
    opacity: 1;
}
/* -- Scroll to Top -- */

@media screen and (max-width: 990px) {
    #toTop {
        bottom: 70px;
        right: 20px;
    }
}